body {
  color: #02122b;
  height: 100vh;
  max-width: 100vw;
  background-image: linear-gradient(135deg, #92ffc0 10%, #002661 100%);
  margin: 0;
  padding: 0;
  font-family: Lexend, sans-serif;
}

h1, h2, h3, h4, h5, h6, p, button, input {
  margin: 0;
  font-family: Lexend, sans-serif;
}

main {
  z-index: 120;
  height: calc(100% - 4rem);
  grid-template-columns: 28rem 1fr;
  gap: 1rem;
  margin: 0;
  padding: 2rem;
  display: grid;
  position: relative;
  overflow: hidden;
}

.sidebar {
  background: #fff;
  border-radius: .5rem;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  display: flex;
  overflow: auto;
  box-shadow: 0 15px 25px #00000026, 0 5px 10px #0000000d;
}

#upload-form {
  flex-direction: column;
  flex: 1;
  gap: 1rem;
  display: flex;
}

button {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .5px;
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #002661 0%, #92ffc0 100%);
  border: 2px solid #0000;
  border-radius: 50px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: .75rem 1rem;
  transition: transform .24s;
  display: inline-flex;
  transform: translateY(0);
  box-shadow: 0 2px 8px #00000026;
}

button:hover {
  border: 2px solid #fff;
  transform: translateY(-2px);
}

button:disabled {
  cursor: not-allowed;
  background: #a5a5a5;
}

button:disabled:hover {
  transform: none;
}

#preview {
  max-width: 100%;
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.form-footer {
  background: #fff;
  gap: .5rem;
  margin: auto -1rem 0;
  padding: 1rem;
  display: flex;
  position: sticky;
  bottom: 0;
  box-shadow: -2px 0 4px #0000000d;
}

#submit-route-btn {
  border: 2px solid #fff;
  flex: 1;
  align-items: center;
  padding: 1rem;
  font-size: 1rem;
  position: sticky;
  bottom: 0;
}

#clear-btn {
  color: initial;
  background: #fff;
  flex: 1;
  margin-top: auto;
  padding: 1rem;
}

.preview__card {
  max-width: 100%;
  max-height: 9rem;
  background: #fff;
  border-radius: .5rem;
  grid-template-columns: 12rem 1fr;
  align-items: center;
  transition: all .24s;
  display: grid;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

.preview__card:hover {
  cursor: pointer;
  transform: translateY(-2px);
  box-shadow: 0 12px 12px -4px #0000001a, 0 12px 8px -1px #0000000f;
}

.preview__card--remove-btn {
  color: #656565;
  opacity: .5;
  box-shadow: none;
  background: none;
  border: none;
  padding: 0;
  font-size: 1.125rem;
  position: absolute;
  top: .25rem;
  right: .25rem;
}

.preview__card--remove-btn:hover {
  color: #820000;
  border: none;
  transform: translateY(0);
}

.preview__card--header {
  overflow: hidden;
}

.preview__card--text {
  padding: 1rem;
  font-size: .875rem;
  overflow: hidden;
}

.preview__card--text h4 {
  text-overflow: ellipsis;
  margin: 0;
  line-height: 1.75;
  overflow: hidden;
}

.preview__card--route {
  box-shadow: none;
  border-bottom: 1px solid #0000001a;
  border-left: 4px solid #1663e0;
  border-radius: 0;
  margin: 0 -1rem;
}

.preview__card--route:hover {
  cursor: initial;
  box-shadow: none;
  transform: none;
}

.preview__card--text dl {
  grid-template-columns: 4rem 1fr;
  align-items: baseline;
  gap: .25rem;
  margin: 0;
  padding: 0;
  display: grid;
}

.preview__card--text dt {
  text-transform: uppercase;
  letter-spacing: .25px;
  color: gray;
  margin: 0;
  padding: 0;
  font-size: 12px;
}

.preview__card--text dd {
  margin: 0;
  padding: 0;
}

.preview__image {
  max-width: 100%;
}

.preview__image:after {
  content: "";
  height: 100%;
  width: 100%;
  display: block;
  position: absolute;
  top: 0;
  box-shadow: inset 3px 3px 10px #000;
}

.sidebar h1 {
  margin: .5rem 0;
  font-size: 2.5rem;
}

.carto {
  width: 100%;
  height: 100%;
  border-radius: .5rem;
  overflow: hidden;
  box-shadow: 0 15px 25px #00000026, 0 5px 10px #0000000d;
}

#map {
  height: 100%;
  z-index: 100;
}

.leaflet-popup-content-photo {
  margin: .5rem;
}

.marker-photo {
  max-width: 100%;
  min-width: 8rem;
}

